@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.page {
  height: 100%;
  background-color: #ffffff;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}
.page__body {
  min-height: 100%;
  min-width: 320px;
  margin: 0;
  position: relative;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px 12px 20px;
}
.page-header__link {
  display: flex;
  align-items: center;
  flex-grow: 1;
}
.page-header__link:hover {
  opacity: 0.8;
}
.page-header__link:active {
  opacity: 0.6;
}
.page-header__logo--text {
  margin: 5px 0 0 calc(50% - 73px);
}
.page-header__burger {
  display: block;
  cursor: pointer;
  border: none;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
}
.page-header__burger::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 18px;
  right: 20px;
  top: 23px;
  background-image: url("../img/burger_open.svg");
  background-position: center;
}
.page-header__burger--closed::before {
  content: "";
  background-color: #ffffff;
  position: absolute;
  width: 20px;
  height: 20px;
  right: 22px;
  top: 21px;
  background-image: url("../img/burger_close.svg");
  background-position: center;
}
@media (max-width: 767px) {
  .page-header__burger--nojs {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header {
    display: grid;
    grid-template-columns: 174px 440px;
    justify-content: space-between;
    align-items: center;
    padding: 28px 30px 0 30px;
    width: 768px;
    margin: 0 auto;
    box-sizing: border-box;
  }
  .page-header__top {
    padding: 0;
  }
  .page-header__burger {
    display: none;
  }
  .page-header__logo--img {
    width: 61px;
    height: 51px;
  }
  .page-header__logo--text {
    margin: 0 10px 0 10px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 53px 110px 0 110px;
    column-gap: 264px;
    align-items: center;
    width: 1440px;
    box-sizing: border-box;
    margin: 0 auto;
  }
  .page-header__top {
    justify-content: start;
  }
  .page-header__logo--img {
    width: 71px;
    height: 59px;
  }
  .page-header__logo--text {
    width: 118px;
    height: 21px;
    margin-left: 13px;
  }
  .page-header--index {
    margin: 53px 0 0 0;
    padding: 0 110px;
    grid-template-columns: 1fr 480px;
    position: absolute;
    left: 50%;
    margin-left: -720px;
  }
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  animation: bounce 0.5s;
}
.main-nav__item {
  border-top: 1px solid #E6E6E6;
}
.main-nav__item:last-child {
  border-bottom: 1px solid #E6E6E6;
}
.main-nav__link {
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 65px;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
}
.main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    max-width: 515px;
    margin-left: auto;
  }
