    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    animation: none;
    margin-top: 15px;
  }
  .main-nav__link {
    font-family: "Oswald", Arial, sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link--active {
    padding-bottom: 4px;
    border-bottom: 2px solid #68B738;
  }
  .main-nav__link--active:hover {
    border-bottom: 2px solid rgba(104, 183, 56, 0.6);
  }
  .main-nav__link--active:active {
    border-bottom: 2px solid rgba(104, 183, 56, 0.3);
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__item {
    border: none;
    margin-bottom: 15px;
  }
  .main-nav__item:last-child {
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    width: 480px;
    margin-left: auto;
  }
  .main-nav__list {
    margin-top: 20px;
  }
  .main-nav__item {
    margin-bottom: 20px;
  }
  .main-nav__link {
    font-size: 20px;
  }
  .main-nav--index .main-nav__link {
    font-size: 20px;
    color: #ffffff;
  }
  .main-nav--index .main-nav__link:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .main-nav--index .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .main-nav--index .main-nav__link--active {
    border-bottom: 2px solid #ffffff;
  }
  .main-nav--index .main-nav__link--active:hover {
    border-bottom: 2px solid rgba(255, 255, 255, 0.6);
  }
  .main-nav--index .main-nav__link--active:active {
    border-bottom: 2px solid rgba(255, 255, 255, 0.3);
  }
}
